
.loader {
    border: 16px solid #f3f3f3; 
    border-top: 16px solid #5e96fc; 
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 2s linear infinite;
    position: relative;
    left: 45%;
    top: 25%;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}